* {
  margin:0;
  padding:0;
  font-size: 0;
  font-family: 'Noto Sans KR', sans-serif;
  cursor: default;
}

body {
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-color: #FFA300;
}

.title {
  color: #ffffff9f;
  font-size: 64pt;
  -ms-user-select: none; 
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
  letter-spacing: -5px;
  font-weight: 300;
}

.items {
  width: 600px;
  max-width: 100vw;
  height: 600px;
  margin-top: 30px;
  background-color: #cccccccc;
  border-radius: 20px;
  padding-top: 10px;
  box-shadow: 2px 2px 5px 1px #5858583d;
}